.card {
  background-color: #357;
  border-radius: 6px;
  height: calc((100vh - 90px) / 11);
  max-height: calc((100vh - 90px) / 11);

  overflow: hidden;
}

.card .front img {
  display: block;
  max-width: 100%;
}

.type2 {
  transform: rotate(90deg);
}

.type3 {
  transform: rotate(180deg);
}

.type4 {
  transform: rotate(-90deg);
}

.player1 {
  width: 26px;
}
